import React from "react"
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { css } from "@emotion/react"
import { useStaticQuery, Link, graphql } from "gatsby"

import "@fontsource/assistant"
import "@fontsource/assistant/300.css"

import { rhythm } from "../utils/typography"

export default function Layout({ children }) {
    const data = useStaticQuery(
      graphql`
        query {
          site {
            siteMetadata {
              title
            }
          }
        }
      `
    )
    return (
    <div
      css={css`
        margin: 0 auto;
        max-width: 700px;
        padding: ${rhythm(2)};
        padding-top: ${rhythm(1.5)};
      `}
    >
      <Link to={`/`}>
        <h3
          css={css`
            margin-bottom: ${rhythm(2)};
            display: inline-block;
            font-style: normal;
          `}
        >
          {data.site.siteMetadata.title}
        </h3>
      </Link>
      <AnchorLink 
        href="#contact"
        css={css`
        margin-left: ${rhythm(1/2)};
        float: right;
      `}
      >
        Contact
      </AnchorLink>
      <AnchorLink 
        href="#blog"
        css={css`
        margin-left: ${rhythm(1/2)};
        float: right;
      `}
      >
        Blog
      </AnchorLink>
      <AnchorLink 
        href="#personal"
        css={css`
        margin-left: ${rhythm(1/2)};
        float: right;
      `}
      >
        Personal
      </AnchorLink>
      <AnchorLink 
        href="#projects"
        css={css`
        margin-left: ${rhythm(1/2)};
        float: right;
      `}
      >
        Projects
      </AnchorLink>
      <AnchorLink 
        href="#about-me"
        css={css`
        margin-left: ${rhythm(1/2)};
        float: right;
      `}
      >
        About Me
      </AnchorLink>
      {children}
    </div>
  )
}